
body {
	text-align: center;
	font-family: 'Roboto Mono', monospace;
      color: #333333;
      margin-right: 60px;
      margin-left: 60px;
}
.carousel {
      position: relative;
      overflow: hidden;
      margin-top: 40px;
      margin-right: 70px;
      margin-left: 70px;
      /*width: 80%;*/
}
.carousel__nav {
      position: absolute;
      top: 50%;
      margin-top: 0px;
      left: 12px;
      background: transparent;
      width: 23px;
      height: 23px;
      opacity: 1;
      border-radius: 40px;
      border: 0;
      font-size: 20px;
      outline: none;
      font-weight: bold;
      color: pink;
      text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
      /*box-shadow: .2px .3px .2px .2px rgba(0, 0, 0, 0.1);*/
}
.carousel__nav:hover {
      color: white;
}
.carousel__nav.carousel__next {
      right: 12px;
      left: auto;
      background: transparent;
      /*box-shadow: .2px .3px .2px .2px rgba(0, 0, 0, 0.1);*/
}
.carousel__nav.carousel__next:hover {
      color: white;
}
.carousel__pagination {
      position: absolute;
      bottom: 10px;
      left: 0;
      right: 0;
      text-align: center;
}
.carousel__pagination button {
      display: inline-block;
      width: 15px;
      height: 15px;
      background-color: pink;
      opacity: 0.8;
      border-radius: 90px;
      margin: 0 2px;
      border: 0;
      /*border: 1px solid white;*/
      box-shadow: 1px 2px 1px 1px rgba(0, 0, 0, 0.3);
}
.carousel__pagination button {
      outline: none;
}
.carousel__pagination button.active {
background-color: white;
}



