
.slide-right-enter-active {
        -webkit-animation: slideRightIn .9s;
                animation: slideRightIn .9s;
}
.slide-right-leave-active {
        -webkit-animation: slideRightOut .9s;
                animation: slideRightOut .9s;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
}
@-webkit-keyframes slideRightIn {
from {-webkit-transform: translateX(100%);transform: translateX(100%);
}
to{-webkit-transform: translateX(0);transform: translateX(0);
}
}
@keyframes slideRightIn {
from {-webkit-transform: translateX(100%);transform: translateX(100%);
}
to{-webkit-transform: translateX(0);transform: translateX(0);
}
}
@-webkit-keyframes slideRightOut {
from {-webkit-transform: translateX(0);transform: translateX(0);
}
to{-webkit-transform: translateX(-100%);transform: translateX(-100%);
}
}
@keyframes slideRightOut {
from {-webkit-transform: translateX(0);transform: translateX(0);
}
to{-webkit-transform: translateX(-100%);transform: translateX(-100%);
}
}
.slide-left-enter-active {
        -webkit-animation: slideLeftIn .9s;
                animation: slideLeftIn .9s;
}
.slide-left-leave-active {
        -webkit-animation: slideLeftOut .9s;
                animation: slideLeftOut .9s;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
}
@-webkit-keyframes slideLeftIn {
from {-webkit-transform: translateX(-100%);transform: translateX(-100%);
}
to{-webkit-transform: translateX(0);transform: translateX(0);
}
}
@keyframes slideLeftIn {
from {-webkit-transform: translateX(-100%);transform: translateX(-100%);
}
to{-webkit-transform: translateX(0);transform: translateX(0);
}
}
@-webkit-keyframes slideLeftOut {
from {-webkit-transform: translateX(0);transform: translateX(0);
}
to{-webkit-transform: translateX(100%);transform: translateX(100%);
}
}
@keyframes slideLeftOut {
from {-webkit-transform: translateX(0);transform: translateX(0);
}
to{-webkit-transform: translateX(100%);transform: translateX(100%);
}
}



